import { Chip, colors } from '@mui/material'
import type { GridColDef, GridRenderCellParams } from '@mui/x-data-grid'

import type { ClubMember } from 'src/models/clubMember'

import { MEMBER_VALUES } from './MemberValues'

const getStatusValue = (params: GridRenderCellParams<ClubMember>) => {
  if (params.row.withdrawalAt) {
    return MEMBER_VALUES.status[3].display
  }
  if (params.row.firedAt) {
    return MEMBER_VALUES.status[2].display
  }
  if (params.value) {
    return MEMBER_VALUES.status[1].display
  }

  return MEMBER_VALUES.status[0].display
}

export const CONFERENCE_REGISTER_COLUMNN: GridColDef[] = [
  { field: 'name', headerName: '이름', flex: 1 },
  { field: 'studentId', headerName: '학번', flex: 1 },
  { field: 'department', headerName: '소속 학부/학과', flex: 1 },
  { field: 'level', headerName: '회원 등급', flex: 1 },
  {
    field: 'isPaused',
    headerName: '활동 상태',
    valueGetter: (params: GridRenderCellParams<ClubMember>) => getStatusValue(params),
    renderCell: (params: GridRenderCellParams<ClubMember>) => {
      let statusColor: string = colors.grey[800]
      let bgColor: string = colors.grey[50]

      switch (params.value) {
        case MEMBER_VALUES.status[3].display:
          statusColor = colors.grey[800]
          bgColor = colors.grey[50]
          break
        case MEMBER_VALUES.status[2].display:
          statusColor = colors.red[800]
          bgColor = colors.red[50]
          break
        case MEMBER_VALUES.status[1].display:
          statusColor = colors.blue[800]
          bgColor = colors.blue[50]
          break
        default:
          statusColor = colors.green[800]
          bgColor = colors.green[50]
          break
      }
      return <Chip label={params.value} size="small" sx={{ color: statusColor, bgcolor: bgColor }} />
    }
  }
]
